.relatedproducts {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 100px;
}

.relatedproducts h1 {
  color: #171717;
  font-size: 50px;
  font-weight: 700;
}

.relatedproducts hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background: #252525;
}

.related-items {
  margin-top: 50px;
  display: flex;
  gap: 20px;
}
